/** @jsx jsx */
import { jsx, Text, Link, Box, Heading } from 'theme-ui'
import React from 'react'

import PropTypes from 'prop-types'
import Section from '../../../../../../../gatsby-theme-jim-components/src/components/system/molecules/containers/Section'

import Article from '../../../../../../../gatsby-theme-jim-components/src/components/system/molecules/containers/Article'
import ServicesSlider from '../../modules/ServicesSlider/ServicesSlider'
import animation from '../../../../../../../gatsby-theme-scroll-reveal/animation'

import Headline from '../../../../../../../gatsby-theme-jim-components/src/components/system/atoms/Text/Headline'
import SectionGrid from '../../layouts/SectionGrid/SectionGrid'

const SlateWays = ({
	sectionBg,
	justifyContent,
	alignItems,
	className,
	containerVariant,
	sectionVariant,
	...props
}) => (
	<>
		{/* <Section
			{...props}
			bg={sectionBg}
			sx={{
				variant: 'layout.sectionWays',
			}}
		>
			<Article
				{...props}
				sx={{
					variant: 'layout.serviceSliderArticle',
					// '& > .slick-slider': {
					// 	transform: `translateX(-32px)`,
					// },
				}}
			> */}

		<SectionGrid
			sectionBg={sectionBg}
			sectionVariant='layout.section__Slider'
			justifyContent='start'
			alignItems='center'
			className='services__section'
			containerVariant='layout.container_Slider'
		>
			<Headline
				{...animation.slideUp1}
				{...props}
				as='h3'
				sx={{
					variant: 'styles.h1',
					px: [2, null, null, null, null],
					// mb: 4,
				}}
			>
				Ways I can help you
			</Headline>
			<Box
				className='servicesSliderContainer'
				{...animation.slideUp2}
				{...props}
				sx={{
					mb: [3, 0, 0, 0, 4],
					variant: 'layout.servicesSlider',

					overflow: ['hidden', 'hidden', 'visible', 'visible', 'visible'],
				}}
			>
				<ServicesSlider />
			</Box>

			<Box
				{...animation.slideUp5}
				{...props}
				sx={{
					mt: [0, 3, 0, 0, 0],
					ml: [0, 2, 0, 0, 0],
				}}
			>
				Interested?
				<Link
					mailto='hi@jamie-johnson.com'
					{...props}
					sx={{
						fontWeight: 'bold',
						fontFamily: 'heading',
						ml: 1,
					}}
				>
					hi@jamie-johnson.com
				</Link>
			</Box>
		</SectionGrid>
		{/* </Article>
		</Section> */}
	</>
)

SlateWays.propTypes = {
	sectionBg: PropTypes.any,
}

SlateWays.defaultProps = {
	sectionBg: 'transparent',
}

export default SlateWays
