/** @jsx jsx */
import { Container, jsx } from 'theme-ui'
import React from 'react'

import PropTypes from 'prop-types'
import FlexCover from '../../../../../../../gatsby-theme-jim-components/src/components/system/molecules/containers/flexBlocks/FlexCover/FlexCover'

const SectionFlex = ({
	containerVariant,
	bg,
	sectionBg,
	justifyContent,
	alignItems,
	className,
	variant,
	sectionVariant,
	containerFlexDirection,
	...props
}) => (
	<>
		<FlexCover
			as='section'
			bg={sectionBg}
			variant={sectionVariant}
			className={className}
		>
			<Container
				className='container'
				{...props}
				sx={{
					// maxWidth: { containerMaxWidth },
					variant: `${containerVariant}`,
					display: 'flex',
					justifyContent: `${justifyContent}`,
					alignItems: `${alignItems}`,
					flexDirection: `${containerFlexDirection}`,
				}}
				// sectionMinHeight='100px'
			>
				{props.children}
			</Container>
		</FlexCover>
	</>
)

SectionFlex.propTypes = {
	alignItems: PropTypes.any,
	className: PropTypes.any,
	containerVariant: PropTypes.any,
	justifyContent: PropTypes.any,
	variant: PropTypes.any,
	sectionVariant: PropTypes.any,
	containerFlexDirection: PropTypes.any,
	children: PropTypes.any,
}

SectionFlex.defaultProps = {
	alignItems: 'center',
	justifyContent: 'center',
	children: 'children',
	containerFlexDirection: 'row',
	containerVariant: 'layout.container',
	children: 'section children',
}

export default SectionFlex
