/** @jsx jsx */
import { jsx, Text, Box, Heading } from 'theme-ui'
import React from 'react'

import PropTypes from 'prop-types'
import FpoImageWireFrame from '../../../../utilities/fpo/FpoImageWireFrame/FpoImageWireFrame'
// import FpoStaticAstronaut from '../../../../utilities/fpo/FpoImages/FpoStaticAstronaut/FpoStaticAstronaut'

const VisualHeadingText = (props) => (
	<Box
		{...props}
		sx={{
			width: `${props.blockWidth}`,
		}}
		className='className'
	>
		<Box
			className='visualContainer'
			{...props}
			sx={{
				height: `${props.visualHeight}`,
				width: `${props.visualWidth}`,
				marginBottom: `${props.visualMb}`,
			}}
			children={props.compImage}
		/>
		<Heading
			{...props}
			sx={{
				variant: `${props.headingVariant}`,
				marginBottom: `${props.headingMb}`,
				pr: 2,
				fontWeight: `${props.headingWeight}`,
				letterSpacing: `${props.headingSpacing}`,
			}}
		>
			{props.headingText}
		</Heading>
		<Text
			{...props}
			className='blockText'
			sx={{
				variant: `${props.textVariant}`,
			}}
			children={props.textText}
		/>
	</Box>
)

VisualHeadingText.propTypes = {
	blockWidth: PropTypes.string,
	buttonText: PropTypes.string,
	buttonTo: PropTypes.string,
	buttonVariant: PropTypes.string,
	compImage: PropTypes.any,
	headingMb: PropTypes.any,
	headingText: PropTypes.any,
	headingVariant: PropTypes.any,
	headingWeight: PropTypes.any,
	visualHeight: PropTypes.string,
	visualMb: PropTypes.any,
	visualWidth: PropTypes.string,
	textMb: PropTypes.number,
	textText: PropTypes.string,
	textVariant: PropTypes.string,
}

VisualHeadingText.defaultProps = {
	blockWidth: '100%',
	compImage: <FpoImageWireFrame />,
	visualHeight: '10em',
	visualMb: 4,
	visualWidth: '3em',
	headingText: 'Headling ullamco sint magna eu id sint',
	headingVariant: 'theme.subline',
	headingWeight: '400',
	headingMb: 4,
	textMb: 4,
	textText:
		'Text lg lorem ea aliqua adipisicing ex sunt culpa in. Nisi ut veniam pariatur ullamco sint magna eu id sint voluptate ullamco laboris.',
	textVariant: 'text.text_lg',
}

export default VisualHeadingText
