/** @jsx jsx */
import { jsx } from 'theme-ui'
import React from 'react'

import ServiceCard from '../../../layouts/ServiceCard'

import WordpressGlyph from '../../../../../../svg/assets/serviceGlyphs/wordpressGlyph.svg'

import { Php } from '@icons-pack/react-simple-icons'
import { DiSass } from 'react-icons/di'
import CapabilitiesIconsBar from '../../../elements/CapabilitiesIconsBar'

const WpServiceCard = (props) => (
	<ServiceCard
		compImage={
			<CapabilitiesIconsBar
				capabilitiesIcon1={<WordpressGlyph />}
				capabilitiesIcon2={<DiSass className='diSassCapaIcon' />}
				capabilitiesIcon3={<Php className='phpCapaIcon' />}
			/>
		}
		visualWidth='10em'
		headingText='Custom Wordpress Sites & Themes'
		textText='From product design to strategy, design systems, UX or UI. I solve complex problems through design, so you can achieve your business goals.'
		linkText='Skills & Frameworks'
		iconName='SolidNorthEastArrow'
	/>
)

WpServiceCard.propTypes = {}

WpServiceCard.defaultProps = {}

export default WpServiceCard
