/** @jsx jsx */
import { jsx } from 'theme-ui'
import React from 'react'

import PropTypes from 'prop-types'
import ServiceCard from '../../../layouts/ServiceCard'
import PlanningGlyph from '../../../../../../svg/assets/serviceGlyphs/planningGlyphv2.svg'

const PlanningServiceCard = (props) => (
	<ServiceCard
		compImage={
			<PlanningGlyph
				{...props}
				sx={{
					overflow: 'visible',
					'.sg15': {
						fill: `rgba(16, 14, 71, 0.5)`,
					},
				}}
			/>
		}
		headingText='Identifying paths to strategic outcomes'
		textText='From product design to strategy, design systems, UX or UI. I solve complex problems through design, so you can achieve your business goals.'
	/>
)

PlanningServiceCard.propTypes = {}

PlanningServiceCard.defaultProps = {}

export default PlanningServiceCard
