/** @jsx jsx */
import { jsx, Container } from 'theme-ui'
import React from 'react'

import PropTypes from 'prop-types'
import GridCover from '../../../../../../../gatsby-theme-jim-components/src/components/system/molecules/containers/grids/GridCover'
// import Article from '../../../../../../../gatsby-theme-jim-components/src/components/system/molecules/containers/Article'

const SectionGrid = ({
	// containerMaxWidth,
	containerVariant,
	bg,
	sectionBg,
	justifyContent,
	alignItems,
	className,
	gridTemplateColumns,
	gridTemplateRows,
	variant,
	sectionVariant,
	...props
}) => (
	<>
		<GridCover
			as='section'
			bg={sectionBg}
			className={className}
			variant={sectionVariant}
		>
			<Container
				className='container'
				variant={containerVariant}
				{...props}
				sx={{
					display: 'grid',
					justifyContent: `${justifyContent}`,
					alignItems: `${alignItems}`,
					gridTemplateColumns: `${gridTemplateColumns}`,
					gridTemplateRows: `${gridTemplateRows}`,
				}}
				// sectionMinHeight='100px'
			>
				{props.children}
			</Container>
		</GridCover>
	</>
)

SectionGrid.propTypes = {
	alignItems: PropTypes.any,
	containerVariant: PropTypes.string,
	justifyContent: PropTypes.any,
	sectionBg: PropTypes.any,
	sectionVariant: PropTypes.any,
	className: PropTypes.any,
	children: PropTypes.any,
}

SectionGrid.defaultProps = {
	// containerVariant: 'layout.container',
	alignItems: 'center',
	containerVariant: 'layout.container',
	justifyContent: 'center',
	gridTemplateColumns: '1fr',
	gridTemplateRows: 'auto',
	sectionBg: 'transparent',
	children: 'section children',
}

export default SectionGrid
