/** @jsx jsx */
import PropTypes from 'prop-types'
import { jsx, Text, Grid, Box, Flex } from 'theme-ui'
import React from 'react'

// import Section from '../../../../../../../gatsby-theme-jim-components/src/components/system/molecules/containers/Section'

import Article from '../../../../../../../gatsby-theme-jim-components/src/components/system/molecules/containers/Article'
import EndorsementCard from '../../layouts/EndorsementCard/EndorsementCard'
import FluidPeizer from '../../../../../hooks/imageHooks/FluidPeizer'
import FluidStrunk from '../../../../../hooks/imageHooks/FluidStrunk'
import animation from '../../../../../../../gatsby-theme-scroll-reveal/animation.js'
import Headline from '../../../../../../../gatsby-theme-jim-components/src/components/system/atoms/Text/Headline'
import Subhead from '../../../../../../../gatsby-theme-jim-components/src/components/system/atoms/Text/Subhead'
import Subline from '../../../../../../../gatsby-theme-jim-components/src/components/system/atoms/Text/Subline'
import Display from '../../../../../../../gatsby-theme-jim-components/src/components/system/atoms/Text/Display'
import SectionGrid from '../../layouts/SectionGrid/SectionGrid'

const NumberBadge = ({ number, ...props }) => (
	<Box
		{...props}
		sx={{
			alignItems: 'center',
			bg: 'primary',
			borderRadius: '50%',
			boxShadow: 'subtleFirmLR',
			color: 'revText',
			display: 'inline-flex',
			fontFamily: 'heading',
			fontSize: [2, 4, 3, 2, 3, 3],
			// height: ['30px', '40px', null, null, null, null],
			height: '40px',
			justifyContent: 'center',
			// lineHeight: 1,
			mr: [2, 3, 3, 3, 3, 3],
			position: 'relative',
			width: '40px',
			// width: ['30px', '40px', null, null, null, null],
		}}
	>
		{number}
	</Box>
)

const BadgeHead = ({ text, ...props }) => (
	<Subline
		{...props}
		as='h3'
		sx={{
			variant: [
				'styles.h4',
				'styles.h2',
				'text.d3',
				'text.d2',
				'text.d2',
				null,
			],
			// fontSize: ['18px', null, null, null, null, null],
			// fontSize: [2, 4, 4, 4, 4, 4],
			display: 'inline-grid',
			// fontWeight: ['normal', 'normal', 'light', 'light', 'light'],
			// letterSpacing: ['normal', 'snug', 'snug', 'snug', 'snug', 'snug'],

			color: 'revText',
			// lineHeight: '1.618em',
		}}
	>
		{text}
	</Subline>
)

const NumberBadgeHead = ({ number, text, ...props }) => (
	<Box
		{...props}
		sx={{
			display: 'inline-block',
			mb: [3, 4, 2, 2, 4, 3],
		}}
	>
		<NumberBadge number={number} />
		<BadgeHead text={text} />
	</Box>
)

const DisplayText = (props) => (
	<Display
		{...props}
		// as='h3'
		sx={{
			variant: ['text.d4', 'text.d2', 'text.d4', 'text.d3', 'text.d3', null],
			maxWidth: [null, null, null, null, null, '22em'],
			color: 'revText',
			pl: [3, 4, 0, 3, 0, 3],
			pr: [null, null, 2, null, null, null],
			mb: [1, 4, null, null, null, null],
		}}
	/>
)

const ParentBox = (props) => (
	<Box
		{...props}
		sx={{
			position: 'relative',
			// mb: [0, 4, 0, 0, 0, 0],
			// pl: [null, null, 4, 3, 5, 4],
			px: [0, 2, 0, 0, 4, 4],

			'&.parentBox1': {
				// pr: [null, null, 4, 4, 4],
				// pl: [null, null, 4, 4, 5, 6],
				// backgroundColor: 'red',
				gridColumn: ['1/3', '1/3', '1/3', '1/2', '1/2', '1/2'],

				order: 1,
			},
			'&.parentBox2': {
				// pr: [null, null, null, 4, 0, null],
				// pl: [null, null, 4, 4, 5, 4],
				// transform: [null, null, 'translateY(48px)', null, null, null],
				// backgroundColor: 'blue',
				gridColumn: ['1/3', '1/3', '1/3', '2/3', '2/3', '2/3'],
				order: [3, 3, 3, 2, 2, 2],
			},
		}}
	/>
)

const SlateValue = ({ sectionBg, sectionVariant, revText, ...props }) => (
	<>
		{/* <Section
			{...props}
			sectionBg={sectionBg}
			className='slate__value'
			sx={{
				variant: 'layout.sectionValue',
			}}
		> */}

		<SectionGrid
			sectionVariant='layout.sectionValue'
			sectionBg={sectionBg}
			justifyContent='center'
			alignItems='center'
			className='value__section'
			containerVariant='layout.container__Value'
		>
			<Article>
				<Grid
					className='articleWrapper'
					{...props}
					sx={{
						px: 0,
						py: 0,
						maxWidth: [null, null, null, '868px', '1024px', '1200px'],
						margin: '0 auto',
						gridTemplateColumns: '1fr 1fr',
						gridTemplateRows: 'auto',
						height: '100%',
					}}
				>
					<Box
						className='articleHeader'
						{...props}
						sx={{
							// width: ['98%', '98%', '98%', '98%', '50%', '60%'],
							// mb: [4, 5, 5, 5, 5, 5],
							gridColumn: '1 / 4',
							mb: [4, 5, null, 4, 4, 4],

							// bg: 'white',
						}}
					>
						<Headline
							{...props}
							as='h4'
							{...animation.slideUp2}
							sx={{
								variant: ['styles.h2', 'styles.h1', null, null, null, null],
								color: 'revText',
								maxWidth: [null, null, null, '83%', '64%', null],
							}}
						>
							Two things you can always expect from me
						</Headline>
					</Box>

					<Grid
						className='articleGrid'
						gap='0px'
						{...props}
						sx={{
							// mb: [3, 3, 3, 4, 4, 4],
							gridColumn: '1/4',
							gridRow: '2/3',
							rowGap: ['32px', '48px', '0px', null, null, null],
							columnGap: ['0', '16px', '32px', null, null, null],
						}}
					>
						<ParentBox className='parentBox1' {...animation.slideUp3NoTrans}>
							<Box
								className='vertDivider'
								{...animation.fadeSlow}
								{...props}
								sx={{
									display: ['none', 'none', 'block', 'block', 'block', 'block'],
									position: 'absolute',
									borderRight: (t) => `1px solid ${t.colors.line}`,
									top: '33%',
									bottom: '33%',
									right: 0,
								}}
							/>
							<NumberBadgeHead number='1' text='Quality Work' />

							<DisplayText>
								Polished, smart and professional products built to grow
								business.
							</DisplayText>
						</ParentBox>
						<ParentBox
							className='parentBox2'
							{...props}
							sx={{
								transform: [null, null, 'translateY(64px)', null, null, null],
							}}
							// bg='red'
						>
							<NumberBadgeHead
								number='2'
								text='Great Service'
								{...animation.slideUp4NoTrans}
							/>

							<DisplayText {...animation.slideUp4NoTrans}>
								Accessible and accountable expertise committed to superior
								service.
							</DisplayText>
						</ParentBox>

						<EndorsementCard
							{...props}
							// {...animation.slideUp3NoTrans}
							className='card1'
							profilePic={<FluidStrunk />}
							textText='...you look back and think of people that you would hire and work with again &mdash; Jamie is one of those'
							endorserName='Merrit Strunk'
							endorserTitle='President, Momentum Financial & Insurance Services'
						/>
						<EndorsementCard
							className='card2'
							profilePic={<FluidPeizer />}
							textText='Jamie brings conviction and passion to the job, and is fun to work with even when he is challenging you'
							endorserName='Dan Peizer'
							endorserTitle='VP Global Marketing & Strategy, Catalent Pharma Solutions'
							{...props}
							sx={{
								transform: [null, null, 'translateY(64px)', null, null, null],
							}}
						/>
					</Grid>
				</Grid>
			</Article>
		</SectionGrid>
	</>
)

SlateValue.propTypes = {
	revText: PropTypes.any,
	sectionBg: PropTypes.any,
}

export default SlateValue
