/** @jsx jsx */
import { jsx, Card, Box } from 'theme-ui'
import React from 'react'

import PropTypes from 'prop-types'
import VisualHeadingText from '../../../../../../../gatsby-theme-jim-components/src/components/system/molecules/contentBlocks/VisualHeadingText'
import FpoImageWireFrame from '../../../../../../../gatsby-theme-jim-components/src/components/utilities/fpo/FpoImageWireFrame'
import CtaLink from '../../../../../../../gatsby-theme-jim-components/src/components/system/organisms/bits/CtaLink/CtaLink'

const ServiceCard = ({ linkText, linkVisual, ...props }) => (
	<>
		<Box className='serviceCardWrapper' marginRight={3}>
			<Card
				className='serviceCard'
				{...props}
				sx={{
					variant: 'layout.serviceSliderCard',
					boxShadow: 'subtleFirmLR',
					display: 'flex',
					flexDirection: 'column',
					alignItems: 'start',
					justifyContent: 'space-between',
				}}
			>
				<Box
					{...props}
					sx={{
						flex: '0 1 auto',
						minHeight: '10em',
						mt: 'auto',
						mb: 'auto',
					}}
				>
					<VisualHeadingText
						blockWidth='100%'
						compImage={props.compImage}
						visualHeight='2.25em'
						visualMb={4}
						visualWidth={props.visualWidth}
						headingText={props.headingText}
						headingVariant='styles.h3'
						headingSpacing='tight'
						headingWeight='heavy'
						headingMb={3}
						textMb={4}
						textText={props.textText}
						textVariant='text.text_lg'
						{...props}
						sx={{
							lineHeight: 1.25,
						}}
					/>
				</Box>
				<CtaLink
					linkText={linkText}
					linkVisual={linkVisual}
					{...props}
					sx={{
						flex: '0 1 auto',
						minHeight: '1em',
						mb: [3, null, null, null, null],
						'& .linkText': {
							fontSize: [1, 3, 3, 3, 2],
							fontFamily: 'heading',
							color: 'primary',
							textDecoration: 'none',
							mr: 2,
						},
						'& .linkVisual': {
							'& > svg': {
								height: ['.5em', '.75em', '.5em', '.5em', '.5em'],
								width: ['.5em', '.75em', '.5em', '.5em', '.5em'],
								fill: 'primary',
							},
						},
					}}
				/>
			</Card>
		</Box>
	</>
)

ServiceCard.propTypes = {
	compImage: PropTypes.any,
	headingText: PropTypes.any,
	iconName: PropTypes.any,
	imageWidth: PropTypes.any,
	linkText: PropTypes.any,
	textText: PropTypes.any,
}

ServiceCard.defaultProps = {
	compImage: <FpoImageWireFrame />,
	headingText: 'Heading text sint mangna eu',
	iconName: 'SolidNorthEastArrow',
	imageWidth: '10em',
	linkText: 'Link',
	textText:
		'Text lg lorem ea aliqua adipisicing ex sunt culpa in. Nisi ut veniam pariatur ullamco sint magna eu id sint voluptate ullamco laboris.',
}
// linkText: '',
// iconName: '',
// compImage: <FpoImageWireFrame />,
// imagewidth: '10em',
// headingText: 'Headling ullamco sint magna eu id sint',

// textText:
// 	'Text lg lorem ea aliqua adipisicing ex sunt culpa in. Nisi ut veniam pariatur ullamco sint magna eu id sint voluptate ullamco laboris.',

export default ServiceCard
